

import React, { Suspense,Component } from 'react'
// import { useSelector, useDispatch } from 'react-redux'
import {
  // CHeader,
  // CToggler,
  // CHeaderBrand,
  // CHeaderNav,
  // CBadge,
  // CButton,
  // CButtonGroup,
  // CCard,
  // CCardBody,
  // CCardFooter,
  // CCardHeader,
  // CCol,
  // CProgress,
  CRow,
  // CCallout
} from '@coreui/react'
import {
  // Card,
  Col,
  // Container,
  Row,
  Spinner,
  // CardBody,
  // CardHeader,
  // CardTitle,
  // FormGroup,
  // Label,
  // Button,
  // Form,
  Input,
  // InputGroupAddon,
  // InputGroupText,
  InputGroup,
  // Breadcrumb,
  // BreadcrumbItem,
} from "reactstrap"
// import CIcon from '@coreui/icons-react'

// import logo from '../../../src/assets/ot.png'
import ModalMessage from '../pages/messages/ModalMessage'

import { CContainer, CFade } from '@coreui/react'
import { format } from 'date-fns';
// import ReactSelect from 'react-select'
// import makeAnimated from 'react-select/animated';
import  { Redirect } from 'react-router-dom'
// import url from '../../conf.js';
// import { storage } from "../../firebase/firebase";
import $ from 'jquery'
import conf from '../../conf.js';
// import ville from '../dict/ville.js'
import Flatpickr from "react-flatpickr"
import { French } from "flatpickr/dist/l10n/fr.js";
// import TheHeaderVisiteur from '../../containers/TheHeaderVisiteur'
// import TheFooter from '../../containers/TheFooter'



const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

class ReservationTerrainVisiteur extends Component {
  constructor(props) {
      super(props)
      this.state = {
        heure_debut:"",
        heure_fin:"",
        date:"",
        status:true,
        terrain:{},
        show:false,
        success:true,
        disponible:false,
        disponibilite:{},
        jour_valide:true,
        boutton:"OK",
        day_load:false,
        prix:{},
        montant:0,
        page2: "",
        message:"Terrain Reservé avec succès !!",
        titre:"Message reservation de terrain",
        temps:[],
        default_time:["00H-01H","01H-02H","02H-03H","03H-04H","04H-05H","05H-06H","06H-07H","07H-08H","08H-09H","09H-10H","10H-11H",
        "11H-12H","12H-13H","13H-14H","14H-15H","15H-16H","16H-17H","17H-18H","18H-19H","19H-20H","20H-21H","21H-22H","22H-23H","23H-24H"]
      }
      this.handleDate.bind(this)
      this.handleChangeHeure.bind(this)
      this.handleTraiteDate.bind(this)
      this.handleAddTransaction.bind(this)

  }
  
  componentDidMount() {
    var jour=["DIMANCHE","LUNDI","MARDI","MERCREDI","JEUDI","VENDREDI","SAMEDI"]

    
    $("#echec").css("display","none")
    localStorage.removeItem('connectReserver')
    this.setState({temps:this.state.default_time})

    console.log("day",new Date().getDay())

    let  data=  JSON.parse(localStorage.getItem('token_auth'))
    console.log("user",data)
    var id=localStorage.getItem("idReservationTerrain")
    try{
    const requestOptions = {
      method: 'GET',
      headers: { 
        'Authorization':'Bearer '+data.token,
  
      },
    };
   

  
    fetch(conf.url+"terrain/terrains/"+id,requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("data",data)
      var prix={}
      $("#total").val("0 fcfa")
      if(data.prix.length){
      jour.map((jr,idx)=>{
        var data_prix= data.prix.find((val) => val.jour==jr)
        console.log("hhh",data_prix)
          prix[""+idx]=data_prix.valeur
          this.setState({prix:prix})
        
      })
    }
      this.setState({terrain:data})
      if(data.indisponibilite!=null){
        this.setState({disponible:true,disponibilite:data.indisponibilite})
     
      }else{
        this.setState({disponible:false})
      }
      
      })
    }catch(error){

    }
  }

  handleChangeHeure=(e)=>{
    var som=0

    this.state.temps.map((val,idx)=>{
      if ($('#checkbox'+idx).is(":checked"))
      {
        
        som+=parseInt(this.state.montant)
        $("#total").val(som+" fcfa")
      }
    })
  }


  handleDate=(date)=>{
    $("#total").val("0 fcfa")
    this.setState({day_load:false,page2:<Spinner size="md" color="black" />,temps:this.state.default_time,montant:0})
    setTimeout(()=>{
      this.handleTraiteDate(date); }, 3000);
  }

  handleTraiteDate= (date)=> {
   console.log("date",format(date[0],'dd/MM/yyyy'))
    const jr= {"LUNDI":1,"MARDI":2,"MERCREDI":3,"JEUDI":4,"VENDREDI":5,"SAMEDI":6,"DIMANCHE":0}
     if(this.state.terrain.prix.length){
     this.setState({montant:this.state.prix[""+date[0].getDay()]})
     }
     if(date.length!==0){
       
       $("#champ-heure").show()
       $("#champ-heure-bouton").show()
       console.log("date:",format(date[0],'dd-MM-yyyy'))
 
       let  token=  JSON.parse(localStorage.getItem('token_auth'))
       const terrain=localStorage.getItem("idReservationTerrain")
       const requestOptions = {
         method: 'POST',
         headers: { 'Content-Type': 'application/json','Accept': 'application/json',
         'Authorization':'Bearer '+token.token,
       },
       body: JSON.stringify({date:format(date[0],'dd/MM/yyyy'),idTerrain:terrain})
       
       };
   
       fetch(conf.url+"terrain/reservations",requestOptions)
       .then((response) => response.json())
       .then((data) => {
         console.log("data reser",data)
         var reserv=";"
         
         if(data.length){
           reserv=data[0]
         }

     
         try{
           
           if(this.state.disponible){
             reserv=reserv+this.state.disponibilite.heure
            
             try{
               for(var i=0;i<this.state.disponibilite.day.split(";").length;i++){
                 if(jr[this.state.disponibilite.day.split(";")[i]]===date[0].getDay()){
                   this.setState({jour_valide:false})
                   break
                 }
               }
 
             }catch(err){
          
 
             }

             reserv.split(';').map((element)=>{
              console.log("data ;",element)
              this.setState({jour_valide:true,temps:this.state.temps.filter(val=>val!=element),day_load:true,page2:<Spinner size="md" color="black" />})
            
            })
             
           }else{
            if(data.length) {
            data[0].split(';').map((element)=>{
              console.log("data ;",element)
              this.setState({jour_valide:true,temps:this.state.temps.filter(val=>val!=element),day_load:true,page2:<Spinner size="md" color="black" />})
            
            })
           }else{
            this.setState({temps:this.state.default_time,day_load:true,jour_valide:true})
           
          }

          }
 
 
         
 
         }catch(error){
         
          this.setState({temps:this.state.default_time,day_load:true})

    
         }
         
       
     
      })
       
       this.setState({date:format(date[0],'dd/MM/yyyy')});
     }else{
       $("#champ-heure").css("display","none")
     }
   
     
   }

  handleReserver=()=>{
   
    const terrain=localStorage.getItem("idReservationTerrain")
    let  token=  JSON.parse(localStorage.getItem('token_auth'))

    let time=""
    let total=0;
   
    this.state.temps.map((val,idx)=>{
      if ($('#checkbox'+idx).is(":checked"))
      {
        time=time+$('#checkbox'+idx).val()+";"
        total+=parseInt(this.state.montant)
      }
    })



   var data = {
      "dateReservation":this.state.date,
      "statePayement":"PENDING",
      "heure":time,
      "idTerrain":terrain,
      "nomTerrain":this.state.terrain.nom,
      "montant":this.state.terrain.prixHeure,
      "montantTotal":total,
      "idUserDoReservation":token.user.id,
      "dateReservationTypeDate":new Date()
    }


    console.log("data a transmettre",data)

    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json','Accept': 'application/json',
      'Authorization':'Bearer '+token.token,
    },
      body: JSON.stringify(data)
  };
  if(data.heure!="" && data.dateReservation!=""){
    fetch(conf.url+"user/add/reservation/user/"+token.user.id+"/terrain/"+terrain,requestOptions)
    .then((response) => response.json())
    .then((data) => {
       console.log("okkk====>",data)
       
    try{
      if(data.id){
       //this.setState({show:true,success:true});
       localStorage.setItem("idReservationVisiteur",data.id)
       var date=token.user.nom+"-"+new Date().getUTCMilliseconds()

       this.handleAddTransaction(date,total)

       window.Payement(date,token.user.nom,token.user.prenom,token.user.email,total)
       
      }else{
       this.setState({show:true,success:false});
      }
      }catch(err){
     this.setState({show:true,success:false});
    }
       
    
     }
     
    );
  }else{
    $("#echec").css("display","block")
  }

    
  } 
  handleCheck=(e)=>{
     console.log("value:",e)
  }

  handleValidate=()=>{
    if(this.state.success){
      this.setState({ok: <Redirect to='/login'/>})
    }else{
      this.setState({show:false})
    }
    
  }

  handleAddTransaction=(number,montant)=>{
    const terrain = localStorage.getItem("idReservationTerrain")
    let  token =  JSON.parse(localStorage.getItem('token_auth'))
    
    var data = {
      "date":new Date(),
      "status":"PENDING",
      "order_number":number,
      "montant":montant,
      "typeTransaction":"RESERVATION_TERRAIN"
    }


    console.log("data a transmettre",data)




    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json','Accept': 'application/json',
      'Authorization':'Bearer '+token.token,
    },
      body: JSON.stringify(data)
  };
  
    fetch(conf.url+"user/add/transaction/user/"+token.user.id+"/terrain/"+terrain,requestOptions)
    .then((response) => response.json())
    .then((data) => {
      localStorage.setItem("idTransactionVisiteur",data.id)
    })
  }
  

  render() {
    // const animatedComponents = makeAnimated();
    const { pick } = this.state
    
  

  return (
    <>

    <CContainer fluid>
    <CRow className="justify-content-center">
    <Suspense fallback={loading}>
    {
     (this.state.status)?
    (
    <CFade>
   

   <ModalMessage
      show={this.state.show}
      success={this.state.success}
      boutton={this.state.boutton}
      handleValidate={this.handleValidate}
      message={this.state.message}
      titre={this.state.titre}
      onCloseClick={() => this.setState({ show: false })}
    />

      <div className="mt-5"></div>

      <section id="contact" class="contact">
        <div class="container">

          <div className="mt-5">
            <div class="section-title">
              <h2>Reservation du terrain - {this.props.terrain}</h2>
            </div>
          </div>

          <div class="row">

            <div class="col-lg-7 mt-5 mt-lg-0 d-flex align-items-stretch" className="justify-content-center">
              <div  class="php-email-form">


                <div class="form-row">
               
                  

                  <div class="container">

                  <div class="form-group col-md-12">
                    <label for="name">Date de réservation</label>
                    <div className="docs-datepicker">
                 
                      <InputGroup>
                        <div className="input-group-append">
                          <button
                            type="button"
                            className="btn btn-outline-secondary docs-datepicker-trigger"
                            disabled
                          >
                            <i
                              className="fa fa-calendar"
                              aria-hidden="true"
                            />
                          </button>
                        </div>
                        <Flatpickr
                          value={pick}
                          className="form-control d-block"
                          placeholder="Choisissez la date de réservation"
                          options={{
                            minDate: "today",
                            locale:French,
                            altInput: true,
                            altFormat: "d/M/Y"
                          }}
                          onChange={this.handleDate}
                        />
                      </InputGroup>
                      <div className="docs-datepicker-container" />
                    </div>
                    <div class="validate"></div>
                  </div>

                  {
                (this.state.day_load)?  
                (((this.state.jour_valide) && (this.state.temps.length))?
                  <>
                    <ul class="ks-cboxtags">
                      {
                        this.state.temps.map((element,idx)=>(
                          <li className="mr-2"><input type="checkbox" onChange={this.handleChangeHeure}  id={"checkbox"+idx} value={element} /><label for={"checkbox"+idx}>{element}</label></li>
                        ))
                      }
                   
                    </ul>
                    <Row className="mt-4">
                      <Col md="8">
                        
                        
                      </Col>
                      <Col md="4">
                        <label>Montant total:</label> 
                        <Input
                          className="form-control"
                          type="text"
                          id="total"
                          readOnly
                        />
                      </Col>
                    </Row>
                    </>:<h2>{"Impossible de réserver a cette date !"}</h2>):(<>{this.state.page2}</>)
                 }
                  </div>
                </div>
                
                <Row className="mt-4">

                  <Col md="12">
                    <div style={{textAlign:"center"}} id="echec" class="alert alert-danger" role="alert">
                      Veuillez Selectionner la période
                    </div>
                  </Col>  
               </Row>

                <div class="mb-3 mt-5">
                  <div class="loading">Chargement</div>
                  <div class="error-message"></div>
                  <div class="sent-message">Message</div>
                </div>
                <div class="text-center"><button onClick={this.handleReserver} id="submit">Réserver le terrain</button></div>
              </div>
            </div>

          </div>

        </div>
      </section>

    </CFade>):(<>{this.state.page}</>)
     }
    </Suspense>
    </CRow>
    </CContainer>

    </>
  )
}
}

export default ReservationTerrainVisiteur
