import {
  Card,
  Col,
  // Container,
  Row,
  Spinner,
  CardBody,
  // CardHeader,
  // CardTitle,
  // FormGroup,
  // Label,
  Button,
  // Form,
  // Input,
  // InputGroupAddon,
  // InputGroupText,
  // InputGroup,
  // Breadcrumb,
  // BreadcrumbItem,
} from "reactstrap"
// import CIcon from '@coreui/icons-react'

// import Carousel from 'react-bootstrap/Carousel'

import { CContainer, CFade } from '@coreui/react'
// import  { Redirect } from 'react-router-dom'

// import portfolio2 from '../dashboard/assets/img/portfolio/portfolio-2.jpg'
import terrain from '../dashboard/assets/img/clients/terrain1.jpg'
import conf from '../../conf.js';
import { storage } from "../../firebase/firebase";
import ReactDOM from 'react-dom';
// import $ from 'jquery'
// import TheHeaderVisiteur from '../../containers/TheHeaderVisiteur'
// import TheFooter from '../../containers/TheFooter'
import ModalDetailTerrain from './ModalDetailSalle'
import ReservationTerrainVisiteur from './ReservationSalleVisiteur'

const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

class NosTerrains extends Component {
  constructor(props) {
      super(props)
      this.state = {
        ok:"",
        terrain:[],
        terrain_init:[],
        page:<div style={{marginTop:"25%",marginBottom:"20%",textAlign:"center"}}>
        <Spinner type="grow" color="black" />
     </div>,
       status:true,
       detailModal: false,
       detail_terrain:{},
      }
      this.handleTerrainClick.bind(this)
      this.handleReserverTerrain.bind(this)
      this.handleRechercheSimple.bind(this)
      
  }
  
  componentDidMount() {
    const requestOptions = {
      method: 'GET',
    
    };

    fetch(conf.url+"terrain/terrains",requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("data",data)
      this.setState({status:false})
      this.setState({terrain:data,terrain_init:data})
      try{

        if(data.length){
          this.setState({status:false})
          data.filter((val)=>val.statusTerrain!="PUBLIC").map((el,idx)=>{
            storage.ref("images")
            .child(el.multimedias[0].file).getDownloadURL()
            .then((url) => {
                
                data[idx].multimedias[0].file=url
                this.setState({terrain:data,terrain_init:data})
            
            })
          })
         
        }
       }catch(error){
        this.setState({terrain:data.filter((val)=>val.statusTerrain!="PUBLIC"),terrain_init:data.filter((val)=>val.statusTerrain!="PUBLIC")})
       }
     }
  
      )
  };

  toggle() {
    this.setState(prevState => ({
      modal: !prevState.modal,
    }))
  }

  handleTerrainClick = (data) => {
  
    this.setState({detailModal:true,detail_terrain:data})
    
    this.toggle()
  }

  handleReserverTerrain=(data)=>{
    localStorage.setItem('idReservationTerrain',data.id);
    var acces=JSON.parse(localStorage.getItem('token_auth'))
    try{
      if(acces.user){
        ReactDOM.render(<div>{<ReservationTerrainVisiteur terrain={data.nom}/>}</div>, document.getElementById('page'));
      }else{
        document.getElementById('login').click()
      }


    }catch(error){
      document.getElementById('login').click()
    }
  
  }

  handleRechercheSimple=(e)=>{
    console.log("donee",e.target.value)
  
    if(e.target.value==""){
      this.setState({terrain:this.state.terrain_init})
    }else{
      var data=this.state.terrain.filter((data) =>  JSON.stringify(data).toLowerCase().indexOf(e.target.value.toLowerCase()) !== -1)
      this.setState({terrain:data})
    }
  
   


}



  render() {

    return (
      <>
        {this.state.ok}
        <CContainer fluid>
          <Suspense fallback={loading}>
          {
        (!this.state.status)?

        (
            <CFade>

            <ModalDetailTerrain
                show={this.state.detailModal}
                terrain={this.state.detail_terrain}
                type="TERRAIN"
                onCloseClick={() => this.setState({ detailModal: false })}
             />
       

              <div className="mt-5"></div>
              <section id="contact" class="contact">
                <div class="container">
                  <section id="terrains" class="portfolio">
                    

                      <div class="section-title">
                        <h2>Nos salles</h2>
                        {/* <p>Magnam dolores commodi suscipit. Necessitatibus eius consequatur ex aliquid fuga eum quidem. Sit sint consectetur velit. Quisquam quos quisquam cupiditate. Et nemo qui impedit suscipit alias ea. Quia fugiat sit in iste officiis commodi quidem hic quas.</p> */}
                      </div>
                      <Row>
                        <div className="form-outline" class="form">
                          {/* <small>Recherche</small> */}
                          <input
                            onChange={this.handleRechercheSimple}
                            type="search"
                            className="form-control "
                            placeholder="Saisissez votre recherche..."
                            style={{borderRadius:"40px"}}
                          />
                        </div>
                      </Row>
                      <div class="row portfolio-container">
                        {
                          this.state.terrain.length?
                          this.state.terrain.map((element,idx)=>(
                            <Col lg="6" md="6" sm="12" className="mt-5">
                              <Card className="block" id="block" style={{borderRadius:"25px",borderColor:"#4f8f3f",borderWidth:"5px",backgroundColor:"#FFFFF"}}>
                                <CardBody>
                              <figure class="terrain" className="text-center">
                                { 
                                  element.multimedias.length?
                                  (<img style={{width:"350px",height:"200px"}} src={element.multimedias[0].file} class="img-fluid" alt=""/>):
                                  (<img style={{width:"350px",height:"200px"}} src={terrain} class="img-fluid" alt=""/>)
                                }
                                <figcaption class="text-center mt-2 font-weight-bold">{element.nom+"/"+element.adresse}</figcaption>
                              </figure>
                              <div class="row">
                                <div class="col-lg-6 col-md-12 col-sm-12 mt-1 text-center">
                                  {/* <Button
                                    color="success"
                                    className="font-16 btn btn-success float-right"
                                    block
                                    onClick={this.handleReserverTerrain.bind(this,element)}
                                  >
                                    Reserver
                                  </Button> */}
                                  <a 
                                    onClick={this.handleReserverTerrain.bind(this,element)}
                                    class="get-started-btn4 text-white text-center a btn-block float-right">Reserver
                                  </a>
                                </div>
                                <div class="col-lg-6 col-md-12 col-sm-12 mt-1 text-center">
                                  {/* <Button
                                    color="success"
                                    className="font-16 btn btn-success float-left"
                                    block
                                    onClick={this.handleTerrainClick.bind(this,element)}
                                  >
                                    Détails
                                  </Button> */}
                                  <a 
                                    onClick={this.handleTerrainClick.bind(this,element)}
                                    class="get-started-btn5 text-white text-center a btn-block float-left">Détails
                                  </a>
                                </div>
                                
                              </div>
                              </CardBody>
                              </Card>
                            </Col>
                            )):<h1 style={{marginTop:30,textAlign:"center"}}>Aucun résultat !</h1>
                        }
                      </div>
                  </section>
                </div>
              </section>
           
            </CFade>):(<>{this.state.page}</>)
            }
          </Suspense>
        </CContainer>
      </>
    )
  }
}

export default NosTerrains
